.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}



* {
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

body {
  font-size: 2rem;
  background: rgb(224, 231, 221);
  color: rgb(78, 60, 82);
}

.wrapper {
  width: 90%;
  height: 100%;
  margin: 0 auto;
}

a {
  color: inherit;
  text-decoration-color: rgb(78, 60, 82);
  text-decoration-thickness: 3px;
  position: relative;
}

a:hover, 
a:focus {
  background: rgba(167, 130, 168, 0.5);
  outline: none;
}


button{
  background: none;
  border: none;
  cursor: pointer;
  background: rgb(167, 130, 168);
  color: white;
  padding: 7px;
  font-size: 1.7rem;
  border: 2px solid transparent;
}

button:focus {
  outline: none;
  border: 2px solid rgb(78, 60, 82);
}

button:hover {
  background: rgb(78, 60, 82);
}

li {
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin: 30px 0;
  border-top: 1.5px dashed purple;
  padding-top: 4px;
  position: relative;
}

label {
  opacity: 0;
  position: absolute;
  z-index: -300;
}